<template>
  <div :class="short ? 'short' : 'space'" @click="getTo()" class="clickable person shadow-15">
    <div class="img" :style="{ 'background-image': 'url(' + require('../assets/img/' + person.code + '.jpg') + ')' }" v-if="!person.boxText"></div>
    <div class="boxText row main-center cross-center" v-else>{{ person.boxText }}</div>
    <div class="label" v-html="person.shortName"></div>
  </div>
</template>
<script>
export default {
  name: 'Person',
  props: {
    person: Object,
    short: true
  },
  methods: {
    getTo() {
      const vm = this;
      let url;
      if (!vm.short) {
        if (vm.person.url) {
          url = vm.person.url;
        } else {
          url = `${vm.$route.params.category}/${vm.person.code}`;
        }
        vm.$router.push(url);
      } else {
        vm.$emit('goTo');
      }
    }
  }
}
</script>
