.person {
  border-radius: 5px;

	.img {
		overflow: hidden;
		border-radius: 5px;
		background-repeat: no-repeat;
		background-size: cover;
		background-position-x: center;
	}

  .boxText {
    font-size: 24px;
    height: 250px;
		width: 150px;
		overflow: hidden;
    text-align: center;
    margin: 10px;
  }
}

